




















































.Index{
  display: grid;
  display: -ms-grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  margin-top: 60px;
}
@media only screen and (max-width: 800px) {
.Index {
       grid-template-columns: 1fr 1fr;
       grid-gap: 30px;
  margin-top: 40px;
}
}
@media only screen and (max-width: 600px) {
.Index {
       grid-template-columns: 1fr;
       grid-gap: 30px;
  margin-top: 40px;
}
}
.Index h4{
  text-align: center;
  margin-top: 0;
}
.Index .ingredient{
  margin: 30px auto;
}
.Index .ingredient li{
  display: inline-block;
}
.Index .delete{
  position: absolute;
  top: 4px;
  right: 4px;
  cursor: pointer;
  color: #aaa;
  font-size: 1.4rem;
}


#AddSmoothies{
    margin-top: 60px;
    padding: 20px;
    max-width: 500px;
}
#AddSmoothies h4{
    font-size: 2rem;
    margin: 30px auto;
}
#AddSmoothies .field{
    margin: 20px auto;
    position: relative;
}
#AddSmoothies .delete{
    position: absolute;
    right: 0;
    bottom: 16px;
    color: #aaa;
    cursor: pointer;
    font-size: 1.4rem;
}

